import styled from '@emotion/styled'
import { Link } from 'gatsby'
import React, { FunctionComponent } from 'react'
import { GatsbyImage } from 'gatsby-plugin-image'
import { PostFrontmatterType } from 'types/PostItem.types'

type PostItemProps = PostFrontmatterType & { link: string }

const PostItemMargin = styled.div`
  width: 100%;
`

const PostItemWrapper = styled(Link)`
  margin: 20px;
  display: flex;
`
const PostImage = styled(GatsbyImage)`
  height: 200px;
  width: 45%;
  margin-right: 20px;

  @media (min-width: 768px) {
    height: 250px;
    width: 50%;
  }
`
const PostContent = styled.div`
  width: 55%;
  height: 200px;
  @media (min-width: 768px) {
    height: 250px;
    width: 50%;
  }
`
const CategoryItemWrap = styled.div`
  display: flex;
  overflow: hidden;
  flex-wrap: wrap;
`
const CategoryItem = styled.span`
  font-size: 12px;
  margin-right: 10px;
  @media (min-width: 768px) {
    font-size: 15px;
  }
`
const CategoryItemTitle = styled.h4`
  font-size: 25px;
  font-weight: bold;
  margin: 5px 0;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  max-height: 200px;
  @media (min-width: 768px) {
    font-size: 28px;
    margin: 10px 0;
  }
`
const CategoryItemDate = styled.p`
  font-size: 16px;
  margin: 5px 0;

  @media (min-width: 768px) {
    font-size: 17px;
    margin: 5px 0;
  }
`
const CategoryItemSummary = styled.p`
  font-size: 15px;
  margin: 10px 0;
  line-height: 1.5;
  max-width: 200px;

  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
  word-wrap: break-word;
  @media (min-width: 768px) {
    -webkit-line-clamp: 5;
    font-size: 16px;
  }
`

const PostItem: FunctionComponent<PostItemProps> = function ({
  title,
  date,
  categories,
  summary,
  thumbnail: {
    childImageSharp: { gatsbyImageData },
  },
  link,
}) {
  return (
    <PostItemMargin>
      <PostItemWrapper to={link}>
        <PostImage image={gatsbyImageData} alt="Post Item Image" />
        <PostContent>
          <CategoryItemTitle>{title}</CategoryItemTitle>
          <CategoryItemDate>{date}</CategoryItemDate>

          <CategoryItemSummary>{summary}</CategoryItemSummary>
          <CategoryItemWrap>
            {categories.map(category => (
              <CategoryItem key={category}>#{category}</CategoryItem>
            ))}
          </CategoryItemWrap>
        </PostContent>
      </PostItemWrapper>
    </PostItemMargin>
  )
}

export default PostItem
