import React, { FunctionComponent } from 'react'
import styled from '@emotion/styled'

const MainCarouselImgWrap = styled.div`
  width: 100%;
  height: 400px;
  background-color: pink;

  @media (min-width: 768px) {
    height: 350px;
  }
`
const MainCarouselImg = styled.img`
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  display: none;
  @media (min-width: 768px) {
    display: block;
  }
`

const MainCarousel: FunctionComponent = function () {
  return (
    <MainCarouselImgWrap>
      <MainCarouselImg />
    </MainCarouselImgWrap>
  )
}

export default MainCarousel
